import 'owl.carousel'


$(document).ready(function() {
    $('.owl-carousel').owlCarousel({
        // items: 1,
        // lazyLoad: true,
        // margin: 10,
        // autoplay: true,
        // autoplayTimeout: 4000,
        // loop: true,
        items: 2, // по-умолчанию 2
        itemsDesktop: false,
        itemsDesktopSmall: [991, 1], // если размер экрана меньше или равно 991 количество слайдов - 1
        itemsTablet: false,
        itemsMobile: false,
    });
});


